body, ul, li {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
}

img {
  width: 40px;
  height: 40px;
}

nav {
  align-items: center;
  height: 100%;
  display: flex;
}

ul {
  list-style-type: none;
  display: flex;
}

ul li {
  margin-left: 20px;
  line-height: 60px;
  display: inline-block;
  position: relative;
}

.link {
  text-transform: uppercase;
  width: 100%;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  display: block;
  position: relative;
}

.is-active:after {
  content: " ";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: none;
  position: absolute;
  bottom: 0;
}

.is-active:hover:after {
  display: block;
}

.is-active:hover {
  color: #00acdc;
}

@media screen and (width <= 1024px) {
  header {
    max-width: 1024px;
  }
}

